@import './global.scss';

.app {
  height: 100vh;
  width: 100vw;
  scroll-behavior: smooth;
  scroll-snap-type: y mandatory;
  overflow-x: hidden;
  scrollbar-width: none;
  -ms-overflow-style: none;

  ::-webkit-scrollbar {
    display:none;
    width: none;
  }
}

.sidebar {
  background-color: rgba(154, 149, 149, 1);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: sticky;
  top: 0;
  padding-left: 30px;
  border-right: 1px solid grey;
  box-shadow: 0px 0px 6px black, inset 0px 0px 1px black;
  cursor: pointer;
  pointer-events: none;
}

.sections {
  display: grid;
  scroll-behavior: smooth;
  scroll-snap-type: y proximity;
  padding-left: 0px;
  overflow: hidden;
  -ms-overflow-style: none;
  ::-webkit-scrollbar {
    display: flex;
    width: none;
  }
}

@media screen and (max-width: 992px) {
  .app {
    .row {
        display: block;
        width: 100vw;
        margin-right: 0px;
        margin-left: 0px;
      }
    .sidebar {
      max-width: 100vw;
      max-height: 15vh;
      z-index: 3;
    }
    .sections {
      max-width: 100vw;
      max-height: 500vh;
      padding-right: 0px;
    }
  }
}
